import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import ware from './module/wareModule'
import activity from './module/activityModule'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    fullscreenLoading:false,
    isFreeze:false, //
    notice: {}, //
    toast: {},
    loading: {
      list:[]
    }, // 
    modal: {
      isShow: false,
    },
    newGoods:true,
    loginError:'', // string
    // 
    productUpload:{
      detail:'',
      images:[]
    },
    //
    categoryAll: {}, // object
    categoryList:[], // array
    clearSelect:{
      isClear:false
    },  //  
    orderList:[],
    //
    mockSendOrder:{
      orderSendFile:null, //,
    },
    logisticsChannel:[],//

    loginCheck:{}, //
    sellerInfo: {}, //
    sellerContact: {}, //


    sellerQualification:{},//



    brandList:[], //



    allCity: {}, //

    currentSearch:{}, //
    currentSearchQuery:{}, //query currentSearch
    searchParams:{},
    //
    msgSearchQuery:{
      isFocus:false
    },
    msgDetail:{},//



    editBussWithMsg:false, // 
    alertMsg:false, //
    alertBussMsg:false,
    sellerInStorageStatus:{},//
    viewImageConfig:{
      isShow:false,
      url:''
    }

  },
  mutations,  // store
  actions,    // store
  modules: {
    ware,
    activity
  },
})
export default store




